@import 'mixins-and-variables';
//BOOTSTRAP ADDITIONAL STYLES

.container {
  min-width: $large-width !important;
}

.btn-add {
    background-color: hsl(47, 100%, 54%) !important;
    background-repeat: repeat-x;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffe899", endColorstr="#ffcc14");
    background-image: -khtml-gradient(linear, left top, left bottom, from(#ffe899), to(#ffcc14));
    background-image: -moz-linear-gradient(top, #ffe899, #ffcc14);
    background-image: -ms-linear-gradient(top, #ffe899, #ffcc14);
    background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #ffe899), color-stop(100%, #ffcc14));
    background-image: -webkit-linear-gradient(top, #ffe899, #ffcc14);
    background-image: -o-linear-gradient(top, #ffe899, #ffcc14);
    background-image: linear-gradient(#ffe899, #ffcc14);
    border-color: #ffcc14 #ffcc14 hsl(47, 100%, 47.5%);
    color: #333 !important;
    text-shadow: 0 1px 1px rgba(255, 255, 255, 0.42);
    -webkit-font-smoothing: antialiased;
}
//5 columns grid for bootstrap
.col-xs-15,
.col-sm-15,
.col-md-15,
.col-lg-15 {
    position: relative;
    min-height: 1px;
    padding-right: 10px;
    padding-left: 10px;
}

.col-xs-15 {
    width: 20%;
    float: left;
}
@media (min-width: 768px) {
.col-sm-15 {
        width: 20%;
        float: left;
    }
}
@media (min-width: 992px) {
    .col-md-15 {
        width: 20%;
        float: left;
    }
}

@media (min-width: 1200px) {
    .col-lg-15 {
        width: 20%;
        float: left;
    }
}

.navbar-brand {
    margin-left: -15px!important;
}
.navbar-header {
    margin-right: 0!important;
    margin-left: 0!important;
    float: left!important;
}
.navbar-collapse.collapse {
    display: block!important;
}

.navbar-nav>li, .navbar-nav{
    float: left!important;
    margin: 0!important;
}
.navbar-nav>li>a {
    padding-top: 15px!important;
    padding-bottom: 15px!important;
}

.navbar-nav .navbar-right:last-child {
    margin-right: -15px !important;
}

.navbar-right {
    float: right!important;
}

.modal-container {
  position: relative;
}

.modal-container .modal, .modal-container .modal-backdrop {
  position: absolute;
}
